<script lang="ts">import { createGrid, createRandomGrid } from "./lib/utils";
import { nextState } from "./lib/game";
import { GRID_SIZE } from "./lib/config";
import Grid from "./components/Grid.svelte";
import Profiler from "./components/Profiler.svelte";
import Controls from "./components/Controls.svelte";
export let gridSize = 0;
let grid = createRandomGrid(gridSize);
let isPlaying = false;
let rafId;
let frames = 0;
let startedPlayingAt;
function handleReset() {
    grid = createGrid(GRID_SIZE);
}
function handleRandom() {
    grid = createRandomGrid(GRID_SIZE);
}
function handleNext() {
    grid = nextState(grid);
}
function handleToggleAtuplay() {
    if (isPlaying) {
        cancelAnimationFrame(rafId);
        isPlaying = false;
        frames = 0;
        startedPlayingAt = undefined;
        return;
    }
    isPlaying = true;
    startedPlayingAt = Date.now();
    step();
}
function step() {
    grid = nextState(grid);
    if (isPlaying) {
        frames++;
        rafId = requestAnimationFrame(step);
    }
}
const actions = {
    reset: handleReset,
    random: handleRandom,
    next: handleNext,
    toggleAutoPlay: handleToggleAtuplay,
};
</script>

<style>
  :root {
    font-size: 16px;
  }

  main {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #333;
    height: 100vh;
  }

  h1 {
    color: white;
  }</style>

<main>
  <h1>Svelte Game of Life</h1>
  <Controls {actions} {isPlaying} />
  <Grid rows={grid} />
  <Profiler {frames} {startedPlayingAt} />
</main>
